
"use client";

import { useState, useEffect, Suspense } from 'react';
import { useSearchParams, useRouter } from 'next/navigation';
import { Textarea } from '@/components/ui/textarea';
import { Progress } from '@/components/ui/progress';
import { motion, AnimatePresence } from 'framer-motion';
import { cn } from '@/lib/utils';

// --- Games ---

function BrainDumpGame({ onThoughtsChange, initialThoughts }: { onThoughtsChange: (thoughts: string) => void, initialThoughts: string }) {
    const [thoughts, setThoughts] = useState(initialThoughts);

    useEffect(() => {
        onThoughtsChange(thoughts);
    }, [thoughts, onThoughtsChange]);

    function RisingParticles() {
        return (
            <div className="absolute inset-0 overflow-hidden -z-10 pointer-events-none">
                <style jsx>{`
                    .particle {
                        position: absolute;
                        bottom: -20px;
                        background-color: #98FB98;
                        border-radius: 50%;
                        opacity: 0;
                        animation: rise 10s infinite ease-in;
                    }
                    @keyframes rise {
                        to {
                            transform: translateY(-100vh);
                            opacity: 0.5;
                        }
                    }
                `}</style>
                {Array.from({ length: 20 }).map((_, i) => {
                    const size = Math.random() * 5 + 2;
                    const style = {
                        width: `${size}px`,
                        height: `${size}px`,
                        left: `${Math.random() * 100}%`,
                        animationDelay: `${Math.random() * 10}s`,
                        animationDuration: `${Math.random() * 5 + 5}s`,
                    };
                    return <div key={i} className="particle" style={style}></div>;
                })}
            </div>
        );
    }

    return (
        <div className="relative flex-1 flex flex-col h-full w-full">
            <RisingParticles />
            <Textarea
                value={thoughts}
                onChange={(e) => setThoughts(e.target.value)}
                placeholder="Let go of your thoughts here..."
                className="flex-1 w-full bg-background/70 backdrop-blur-sm border-2 border-primary/50 focus:border-primary text-lg resize-none p-4"
            />
        </div>
    );
}

const bubbleColors = [
    'bg-red-300/70',
    'bg-yellow-300/70',
    'bg-green-300/70',
    'bg-blue-300/70',
    'bg-purple-300/70',
];

type Bubble = {
    id: number;
    x: number;
    y: number;
    size: number;
    color: string;
    vx: number;
    vy: number;
};

function BubblePopGame() {
    const [bubbles, setBubbles] = useState<Bubble[]>([]);

    useEffect(() => {
        const initialBubbles = Array.from({ length: 15 }, (_, i) => createBubble(i));
        setBubbles(initialBubbles);
    }, []);

    const createBubble = (id: number): Bubble => ({
        id: id,
        x: Math.random() * 80 + 10,
        y: Math.random() * 80 + 10,
        size: Math.random() * 40 + 20,
        color: bubbleColors[Math.floor(Math.random() * bubbleColors.length)],
        vx: (Math.random() - 0.5) * 0.5,
        vy: (Math.random() - 0.5) * 0.5,
    });
    
    const popBubble = (id: number) => {
        setBubbles(prev => prev.filter(b => b.id !== id));
        setTimeout(() => {
            setBubbles(prev => [...prev, createBubble(Date.now())]);
        }, 500);
    };

    useEffect(() => {
        const interval = setInterval(() => {
            setBubbles(currentBubbles =>
                currentBubbles.map(b => {
                    let newX = b.x + b.vx;
                    let newY = b.y + b.vy;
                    let newVx = b.vx;
                    let newVy = b.vy;

                    if (newX <= 0 || newX >= 100 - (b.size / 4)) newVx *= -1;
                    if (newY <= 0 || newY >= 100 - (b.size / 4)) newVy *= -1;

                    return { ...b, x: newX, y: newY, vx: newVx, vy: newVy };
                })
            );
        }, 1000 / 60);
        return () => clearInterval(interval);
    }, []);

    return (
        <div className="relative w-full h-full flex-1 bg-red-100/30 rounded-lg overflow-hidden">
             <AnimatePresence>
                {bubbles.map((bubble) => (
                    <motion.div
                        key={bubble.id}
                        layout
                        initial={{ scale: 0, opacity: 0 }}
                        animate={{ scale: 1, opacity: 1 }}
                        exit={{ scale: 0, opacity: 0 }}
                        transition={{ type: 'spring', stiffness: 260, damping: 20 }}
                        className={cn("absolute rounded-full cursor-pointer border-2 border-white/50", bubble.color)}
                        style={{
                            width: bubble.size,
                            height: bubble.size,
                            top: `${bubble.y}%`,
                            left: `${bubble.x}%`,
                            transform: 'translate(-50%, -50%)',
                        }}
                        onClick={() => popBubble(bubble.id)}
                        whileHover={{ scale: 1.1 }}
                        whileTap={{ scale: 0.9 }}
                    />
                ))}
            </AnimatePresence>
        </div>
    )
}


function PlaySessionContent() {
  const router = useRouter();
  const searchParams = useSearchParams();
  const mood = searchParams.get('mood');
  
  const [timeLeft, setTimeLeft] = useState(180); // 3 minutes
  const [thoughts, setThoughts] = useState('');

  useEffect(() => {
    if (!mood && typeof window !== 'undefined') {
        router.push('/mood');
    }
  }, [mood, router]);

  useEffect(() => {
    if (timeLeft <= 0) {
      localStorage.setItem('mindspring-thoughts', thoughts);
      router.push(`/session/result?mood=${mood}`);
      return;
    }

    const timer = setInterval(() => {
      setTimeLeft(prev => prev - 1);
    }, 1000);

    return () => clearInterval(timer);
  }, [timeLeft, router, mood, thoughts]);

  const minutes = Math.floor(timeLeft / 60);
  const seconds = timeLeft % 60;
  
  const progress = ((180 - timeLeft) / 180) * 100;
  
  const renderGame = () => {
    switch (mood) {
        case 'Stressed':
            return <BubblePopGame />;
        case 'Full Mind':
            return <BrainDumpGame onThoughtsChange={setThoughts} initialThoughts={thoughts} />;
        default:
            return <BrainDumpGame onThoughtsChange={setThoughts} initialThoughts={thoughts} />;
    }
  }

  return (
    <div className="relative flex-1 flex flex-col p-6 h-full w-full">
      <header className="flex justify-between items-center mb-4">
        <div className="text-2xl font-bold font-mono text-foreground">
            {String(minutes).padStart(2, '0')}:{String(seconds).padStart(2, '0')}
        </div>
      </header>
      <main className="flex-1 flex flex-col">
        {renderGame()}
      </main>
      <footer className="mt-4">
        <div className="flex items-center gap-4">
          <span className="text-sm font-medium">Clarity</span>
          <Progress value={progress} className="w-full h-3 [&>div]:bg-gradient-to-r [&>div]:from-secondary [&>div]:to-yellow-400" />
        </div>
      </footer>
    </div>
  );
}

export default function PlaySessionPage() {
    return (
        <Suspense fallback={<div>Loading session...</div>}>
            <PlaySessionContent />
        </Suspense>
    )
}
